import { MSinglex } from '../m-singlex.vue';

export default {
    name: 'u-navbar',
    childName: 'u-navbar-item',
    extends: MSinglex,
    props: { router: { type: Boolean, default: true } },
    created() {
        this.$on('select', ({ itemVM }) => this.router && itemVM.navigate());
    },
};
